import { IApplication } from "../types";
;
export const mapToApplications = (response: any[]):IApplication[] => response.map((applicationData) =>
  new Application(
    applicationData.id || null,
    applicationData.applicant_name || null,
    applicationData.monday_board_name|| null,
    applicationData.status || null,
    applicationData.comment || null,
    applicationData.visa_application ? mapToVisaApplication(applicationData.visa_application) : null,
    applicationData.active || null, 
    applicationData.status_comment || null,
    applicationData.status_title || null,
    applicationData.status_icon || null,
    applicationData.status_icon_color || null
  )
);

const mapToVisaApplication = (applicationData) => { 
  return new Application(
    applicationData.id || null,
    applicationData.applicant_name || null,
    applicationData.monday_board_name|| null,
    applicationData.status || null,
    applicationData.comment || null,
    null,
    applicationData.active,
    applicationData.status_comment || null,
    applicationData.status_title || null,
    applicationData.status_icon || null,
    applicationData.status_icon_color || null
  )
};

class Application implements IApplication {
  constructor(
    public id: number,
    public applicantName: string,
    public mondayBoardName: string,
    public status: string,
    public comment: string,
    public visaApplication: object,
    public active: boolean,
    public statusComment: string,
    public statusTitle: string,
    public statusIcon: string,
    public statusIconColor: string,
  ) { }
}
