import { AuthAction, AuthState, SetSignedInAction, SetLoadingAction } from '../types'
import { AUTH_ACTION_TYPES } from './actions'

const initialState: AuthState = {
  signedIn: false,
  loading: true
};

export const auth = (
  state: AuthState = initialState,
  action: AuthAction
): AuthState => {
  switch (action.type){
    case AUTH_ACTION_TYPES.SET_SIGNED_IN:
      return { 
        ...state,
        signedIn: (<SetSignedInAction>action).payload,
      }
    case AUTH_ACTION_TYPES.SET_LOADING:
      return { 
        ...state,
        loading: (<SetSignedInAction>action).payload,
    }
    default:
      return {...state};
  }
}