import { SetApplicationsAction, ApplicationsAction , ApplicationsState } from '../types'
import { APPLICATIONS_ACTION_TYPES } from './actions'

const initialState: ApplicationsState = [];

export const applications = (
  state: ApplicationsState = initialState,
  action: ApplicationsAction
): ApplicationsState => {
  switch (action.type){
    case APPLICATIONS_ACTION_TYPES.SET_APPLICATIONS:
      return (<SetApplicationsAction>action).payload;
    default:
      return [...state];
  }
}