import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
//import { createSwitchNavigator } from '@react-navigation/compat';
import * as React from 'react';
import { RootStackParamList } from '../types';
import Welcome from '../screens/Welcome'
import SignIn from '../screens/SignIn'
import DrawerNavigation from './DrawerNavigator';
import Pin from '../screens/Pin';
import { connect } from 'react-redux';
import { AppState, AuthState } from '../state/types';

const Navigation = ({signedIn, loading}: AuthState) => {
  
  return (
    <NavigationContainer>
      <RootNavigator signedIn={signedIn} loading={loading}/>
    </NavigationContainer>
  );
}

const mapStateToProps = (state: AppState) => ({
  signedIn: state.auth.signedIn,
  loading: state.auth.loading
});

export default connect(mapStateToProps)(Navigation);

const Stack = createStackNavigator<RootStackParamList>();

const RootNavigator = ({signedIn, loading}: {signedIn: boolean, loading: boolean}) => {
  return (
    <Stack.Navigator screenOptions={{ headerShown: false }} initialRouteName="Welcome">
      { loading && !signedIn && <Stack.Screen name="Welcome" component={Welcome} /> }
      { signedIn ? 
        <Stack.Screen name="DrawerNavigation" component={DrawerNavigation} />
        :
        <>
          <Stack.Screen name="SignIn" component={SignIn} />
          <Stack.Screen name="Pin" component={Pin} />
        </>
      }
    </Stack.Navigator>
  );
}

