import React, { useEffect } from 'react';
import { DrawerParamList } from '../types';
import { DrawerScreenProps } from '@react-navigation/drawer';
import { Container, Header, Title, Left, Icon, Right, Button, Body, Content,Text, Card, CardItem, List, ListItem, View } from "native-base";
import { connect, Dispatch } from 'react-redux';
import { AppState, IApplication } from '../state/types';
import { setApplications } from '../state/applications/actions';
import { getApiClient } from '../service/ApiClient';
import { RefreshControl, StyleSheet, ActivityIndicator } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import Colors from '../constants/Colors';
import Status from '../components/Status';
import { API_TOKEN } from "../constants/Http";
import { setSignedIn, setLoading } from '../state/auth/actions';

const Logout = ({navigation, route, onLogout}: DrawerScreenProps<DrawerParamList, 'Logout'>) => {

  useEffect(() => {
    AsyncStorage.removeItem(API_TOKEN);
    AsyncStorage.removeItem('email');
    AsyncStorage.removeItem('pin');
    setTimeout(() => {
      onLogout();
    }, 2000); 
  }, []);

  return (
    <Container>
      <Header style={styles.header}>
        <Left>
          <Button
            transparent
            onPress={() => navigation.goBack()}
          >
            <Icon name="arrow-back" style={{color: Colors.lightText}} />
          </Button>
        </Left>
        <Body>
          <Text style={styles.title}>Logout</Text>
        </Body>
        <Right />
      </Header>
      <Content style={styles.content}>
        <ActivityIndicator size={50}/>
      </Content>
    </Container>
  );
}

const mapStateToProps = (state: AppState) => ({
  auth: state.auth
});

const mapDipatchToProps = (dispatch: Dispatch) => ({
  onLogout: () => {
    dispatch(setSignedIn(false));
  },
});

export default connect(mapStateToProps, mapDipatchToProps)(Logout)

const styles = StyleSheet.create({
  title: {
    flexDirection: "row",
    fontSize: 16,
    color: Colors.lightText
  },
  header: {
    backgroundColor: Colors.main,
  },
  applicationTitle: {
    color: Colors.action,
    fontWeight: 'bold',
    fontSize: 18
  },
  content: {
    flex: 1,
  }
});
