import { IApplication, SetApplicationsAction } from '../types'

export const APPLICATIONS_ACTION_TYPES = {
  SET_APPLICATIONS: 'APPLICATIONS/SET_APPLICATIONS',
  UPDATE_APPLICATIONS: 'APPLICATIONS/UPDATE_APPLICATIONS'
}

export const setApplications = (applications: IApplication[]): SetApplicationsAction => ({
  type: APPLICATIONS_ACTION_TYPES.SET_APPLICATIONS,
  payload: applications
});