import { UserAction, UserState, SetUserAction } from '../types'
import { USER_ACTION_TYPES } from './actions'

const initialState: UserState = {
  name: '',
  surname: '',
  email: ''
};

export const user = (
  state: UserState = initialState,
  action: UserAction
): UserState => {
  switch (action.type){
    case USER_ACTION_TYPES.SET_USER:
      let payload  = <SetUserAction>action;
      return { 
        name:payload.userData.name,
        surname: payload.userData.surname,
        email: payload.userData.email 
      }
    default:
      return {...state};
  }
}