import React, { useEffect } from 'react';
import { JobStackParamList } from '../types';
import { DrawerScreenProps } from '@react-navigation/drawer';
import { Container, Header, Title, Left, Icon, Right, Button, Body, Content,Text, Card, CardItem, List, ListItem, View, Toast } from "native-base";
import { connect, Dispatch } from 'react-redux';
import { AppState, IApplication } from '../state/types';
import { setApplications } from '../state/applications/actions';
import { getApiClient } from '../service/ApiClient';
import { ScrollView } from 'react-native-gesture-handler';
import { RefreshControl, StyleSheet, ActivityIndicator } from 'react-native';
import Colors from '../constants/Colors';
import Status from '../components/Status';
import { UnauthorizedError } from '../errors/api/UnauthorizedError';

const JobList = ({navigation, applications, onSetApplications}: DrawerScreenProps<JobStackParamList, 'JobList'>) => {

  const [refreshing, setRefreshing] = React.useState(false);
  const [loading, setLoading] = React.useState(false);

  const onRefresh = () => {
    setRefreshing(true);
    getApiClient().fetchApplications().then(applcations => {
      console.log(applcations);
      onSetApplications(applcations);
      setRefreshing(false);
    }).catch(e => {
      if(e instanceof UnauthorizedError) {
        navigation.navigate('Logout');
      }
      displayErrorMessage('Could not connect to Server. Try again later.');
      setRefreshing(false);
    });
  }

  useEffect(() => {
    setLoading(true);
    getApiClient().fetchApplications().then(applcations => {
      onSetApplications(applcations);
      setLoading(false);
    }).catch(e => {
      if(e instanceof UnauthorizedError) {
        navigation.navigate('Logout');
      }
      displayErrorMessage('Could not connect to Server. Try again later.');
      setLoading(false);
    });
  }, []);

  const displayErrorMessage = (message: string) => {
    Toast.show({
      text: message,
      type: 'danger',
      duration: 5000,
      position: "top"
    })
  }
  return (
    <Container>
      <Header style={styles.header}>
        <Left>
          <Button
            transparent
            onPress={() => navigation.openDrawer()}
          >
            <Icon name="menu" style={{color: Colors.lightText}} />
          </Button>
        </Left>
        <Body>
          <Text style={styles.title}>My Applications</Text>
        </Body>
        <Right />
      </Header>
      <Content
        refreshControl={
          <RefreshControl refreshing={refreshing} onRefresh={onRefresh} />
        }
      >
      { loading && <ActivityIndicator size={50}/>}
      { applications.map((application: IApplication, index: number) => 
        <ListItem key={index} style={{...styles.listItem, ...(application.active && {backgroundColor: '#d0e8d5'}) }}>
          <Body style={styles.jobData}>
            <Text numberOfLines={1} style={styles.applicationTitle}>{application.mondayBoardName}</Text>
            <Status style={styles.status} application={application.visaApplication || application}>
              {application.visaApplication ? application.visaApplication.status : application.status}
            </Status>
          </Body>
          <Right style={styles.listButtonContainer}>
            <Button transparent
              onPress={() => {
                navigation.navigate('Job', {applicationId: application.id});
              }}
            >
              <Icon name="arrow-forward"/>
            </Button>
          </Right>
        </ListItem> 
        )
      }
      </Content>
    </Container>
  );
}

const mapStateToProps = (state: AppState) => ({
  user: state.user,
  applications: state.applications
});

const mapDipatchToProps = (dispatch: Dispatch) => ({
  onSetApplications: (applcations: IApplication[]) => {
    dispatch(setApplications(applcations.reverse()));
  },
});

const styles = StyleSheet.create({
  status: {
    paddingTop: 4,
    paddingLeft: 8
  },
  title: {
    flexDirection: "row",
    fontSize: 16,
    color: Colors.lightText
  },
  header: {
    backgroundColor: Colors.main,
  },
  applicationTitle: {
    color: Colors.action,
    fontWeight: 'bold'
  },
  listItem: {
    marginLeft: 0,
    paddingLeft: 5
  },
  jobData: {
    flexDirection: 'column',
  },
  listButtonContainer:{
    flex: 0.20,
  }
});

export default connect(mapStateToProps, mapDipatchToProps)(JobList);
