import * as React from 'react';
import { useForm, Controller } from "react-hook-form";
import { Button, Text, Input, Form, Item, Label, View, Spinner } from 'native-base';
import ErrorLabel from '../../components/form/ErrorLabel';
import { StyleSheet, ImageBackground, Dimensions } from 'react-native';

type FormValues = {
  pin: string;
}

const PinNumberForm: React.FC<{onSuccess: (data: FormValues) => void, sendingInProgress: boolean}> = 
  ({onSuccess, sendingInProgress}) => {

  const { control, handleSubmit, errors } = useForm<FormValues>();
  const onSubmit = (data: FormValues) => onSuccess(data);
  
  return (
    <ImageBackground source={require('../../assets/images/login-map-bg.png')} style={styles.image} resizeMode="contain">      
      <View style={styles.headerTextContainer}>
        <Text style={styles.headerText}>Login</Text>
      </View>
      <View style={styles.headerTextContainer}>
        <Text style={styles.headerDescription}>Check your mailbox and enter the PIN you received in the message</Text>
      </View>
      <Form>
        <Controller
          control={control}
          render={({ onChange, onBlur, value }: any) => (
            <Item style={styles.input} inlineLabel>
              <Label>PIN</Label>
              <Input
                secureTextEntry={true}
                onBlur={onBlur}
                onChangeText={value => onChange(value)}
                value={value}
              />
            </Item>
          )}
          name="pin"
          rules={{ required: 'This filed is required' }}
          defaultValue=""
        />
        { errors.pin && errors.pin.message && <ErrorLabel>{errors.pin.message}</ErrorLabel>}
        <View style={styles.buttonContainer}>
          <Button onPress={handleSubmit(onSubmit)} disabled={sendingInProgress} style={styles.submitButton}>
            { !sendingInProgress ? <Text>LOGIN</Text> : <Spinner color={'#ffffff'}/> }
          </Button>
        </View>
      </Form>
    </ImageBackground>
  );
}

const styles = StyleSheet.create({
  headerTextContainer: {
    flexDirection: 'row',
    justifyContent: 'center'
  },
  headerText: {
    fontSize: 25,
    paddingBottom: 30,
    color: '#212121'
  },
  headerDescription: {
    textAlign: 'center',
    fontSize: 14,
    paddingBottom: 30,
    color: '#848484',
    paddingLeft: 20,
    paddingRight: 20,
  },
  buttonContainer: {
    paddingTop:30,
    padding: 10,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center'
  },
  input: {
    backgroundColor: '#ffffff',
    borderColor: '#dedce0',
    borderWidth: 1
  },
  image: {
    flex: 1,
    height: Dimensions.get('window').height/ 2,
    justifyContent: "center"
  },
  submitButton: {
    minWidth: 120,
    justifyContent: 'center'
  }
});

export default PinNumberForm;
