import * as React from 'react';
import { StyleSheet, Dimensions, ImageBackground, Image } from 'react-native';
import { RootStackParamList } from '../types';
import { StackScreenProps } from '@react-navigation/stack';
import { Container, Toast, Grid, Row, Content } from 'native-base';
import PinNumberForm  from '../forms/sign-in/PinNumberForm';
import { getApiClient } from '../service/ApiClient';
import { ResourceNotFoundError } from '../errors/api/ResourceNotFoundError';
import { ApiError } from '../errors/api/ApiError';
import { ApiConnectionError } from '../errors/api/ApiConnectionError';
import { UndefinedApiTokenError } from '../errors/api/UndefinedApiTokenError';
import { UnauthorizedError } from '../errors/api/UnauthorizedError';
import { CommonActions } from '@react-navigation/native';
import { setSignedIn, setLoading } from '../state/auth/actions';
import { Dispatch } from 'redux';
import { AppState } from '../state/types';
import { connect } from 'react-redux';

function Pin({navigation, route, onSignedIn}: StackScreenProps<RootStackParamList>) {

  const [sendingInProgress, setSendingInProgress] = React.useState<boolean>(false);

  const authenticate = ({email, pin} : {email:string, pin: string}): void => {
    setSendingInProgress(true);
    getApiClient().authenticate(email, pin)
      .then(() => {
        onSignedIn()
      }).catch((error: Error) => {
        handleError(error);
      }).finally(() => setSendingInProgress(false));
  }

  const handleError = (error: Error): void => {
    let errorMessage = '';
    switch (error.constructor){
      case UnauthorizedError:
        errorMessage = "Provided PIN number is not valid."
        break;
      case ApiError:
        errorMessage = 'Server connection error.'
        break;
      case UndefinedApiTokenError:
        errorMessage = 'Authentication failed.'
      case ApiConnectionError:
        errorMessage = 'Could not connect to Server. Try again later.'
        break;
    }
    displayErrorMessage(errorMessage);
  }

  const displayErrorMessage = (message: string) => {
    Toast.show({
      text: message,
      type: 'danger',
      duration: 3000,
      position: "top"
    })
  }

  return (
    <Container style={styles.container}>
      <Content style={{flex: 1}}>
      <Grid>
        <Row style={styles.headerContainer}>
          <ImageBackground 
            source={require('../assets/images/login-bg.png')} 
            style={styles.image} 
            resizeMode="contain"
          >
            <Image source={require('../assets/images/logo.png')} 
              resizeMode="contain" 
              style={{height:Dimensions.get('window').height/ 4, width:Dimensions.get('window').width}} 
            />
          </ImageBackground>
        </Row>
        <Row style={styles.formContainer}>
          <PinNumberForm 
            onSuccess={({pin}) => authenticate({pin, email: route.params.email})}
            sendingInProgress={sendingInProgress}
          />
        </Row>
      </Grid>
      </Content>
    </Container>
  );
}

const mapStateToProps = (state: AppState) => ({
  auth: state.auth
});

const mapDipatchToProps = (dispatch: Dispatch) => ({
  onSignedIn: () => {
    dispatch(setSignedIn(true));
    dispatch(setLoading(false));
  },
});

export default connect(mapStateToProps, mapDipatchToProps)(Pin)


const styles = StyleSheet.create({
  image: {
    flex: 1,
    height: Dimensions.get('window').height/ 2,
    justifyContent: "center"
  },
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'row',
  },
  formContainer: {
    flex: 1
  },
  headerContainer: {
    flex:1,
    backgroundColor: '#3e9b83',
    height: Dimensions.get('window').height/ 2
  }
});