import { SetSignedInAction, SetLoadingAction } from '../types'

export const AUTH_ACTION_TYPES = {
  SET_SIGNED_IN: 'AUTH/SET_IS_SIGNED_IN',
  SET_LOADING: 'AUTH/SET_IS_LOADING'
}

export const setSignedIn = (signedIn: boolean): SetSignedInAction => ({
  type: AUTH_ACTION_TYPES.SET_SIGNED_IN,
  payload: signedIn
});

export const setLoading = (loading: boolean): SetLoadingAction => ({
  type: AUTH_ACTION_TYPES.SET_LOADING,
  payload: loading
});