import * as React from 'react';
import { StyleSheet, Dimensions, ImageBackground, Image } from 'react-native';
import { RootStackParamList } from '../types';
import { StackScreenProps } from '@react-navigation/stack';
import { Container, Toast, Grid, Row, Content, Text } from 'native-base';
import CredentialsForm from '../forms/sign-in/CredentialsForm';
import { getApiClient } from '../service/ApiClient';
import { ResourceNotFoundError } from '../errors/api/ResourceNotFoundError';
import { ApiError } from '../errors/api/ApiError';
import { ApiConnectionError } from '../errors/api/ApiConnectionError';
import Colors from '../constants/Colors';

export default function SignIn({navigation}: StackScreenProps<RootStackParamList>) {

  const [sendingInProgress, setSendingInProgress] = React.useState<boolean>(false);

  const verifyEmail = ({email} : {email: string}): void => {
    setSendingInProgress(true);
    getApiClient().verifyEmail(email)
      .then(() => {
        navigation.navigate('Pin', {email})
      }).catch((error: Error) => {
        handleError(error);
      }).finally(() => setSendingInProgress(false))
  }

  const handleError = (error: Error): void => {
    let errorMessage = '';
    switch (error.constructor){
      case ResourceNotFoundError:
        errorMessage = "Provided email address doesn't match our records"
        break;
      case ApiError:
        errorMessage = 'Server connection error.'
        break;
      case ApiConnectionError:
        errorMessage = 'Could not connect to Server. Try again later.'
        break;
    }
    displayErrorMessage(errorMessage);
  }

  const displayErrorMessage = (message: string) => {
    Toast.show({
      text: message,
      type: 'danger',
      duration: 5000,
      position: "top"
    })
  }

  return (
    <Container style={styles.container}>
      <Content style={{flex: 1}}>
      <Grid>
        <Row style={styles.headerContainer}>
          <ImageBackground 
            source={require('../assets/images/login-bg.png')} 
            style={styles.image} 
            resizeMode="stretch"
          >
            <Image source={require('../assets/images/logo.png')} 
              resizeMode="contain" 
              style={{height:Dimensions.get('window').height/ 4, width:Dimensions.get('window').width}} 
            />
          </ImageBackground>
        </Row>
        <Row style={styles.formContainer}>
          <CredentialsForm 
            onSuccess={(data) => verifyEmail(data)}
            sendingInProgress={sendingInProgress}
          />
        </Row>
      </Grid>
      </Content>
    </Container>
  );
}

const styles = StyleSheet.create({
  image: {
    flex: 1,
    height: Dimensions.get('window').height/ 2,
    width: Dimensions.get('window').width,
    justifyContent: "center"
  },
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'row',
  },
  formContainer: {
    flex: 1
  },
  headerContainer: {
    flex:1,
    backgroundColor: '#3e9b83',
    height: Dimensions.get('window').height/ 2
  }
});
