import React, { useEffect } from 'react';
import { StyleSheet, Dimensions, ImageBackground, Image } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { RootStackParamList } from '../types';
import { StackScreenProps } from '@react-navigation/stack';
import { Container, Row } from 'native-base';
import { connect } from 'react-redux';
import { AppState } from '../state/types';
import { Dispatch } from 'redux';
import { setSignedIn, setLoading } from '../state/auth/actions';

function Welcome({navigation, route, onSignedIn, onSignedOut}: StackScreenProps<RootStackParamList>) {
  useEffect(() => {
    AsyncStorage.getItem('pin').then(pin => {
      if (pin) {
        onSignedIn();
      } else {
        onSignedOut();
      }
    }).catch(e => {
      console.warn(e);
      navigation.replace('SignIn');
    });
  }, []);

  return (
    <Container style={styles.container}>
        <Row style={styles.headerContainer}>
          <ImageBackground 
            source={require('../assets/images/login-bg.png')} 
            style={styles.image} 
            resizeMode="stretch"
          >
            <Image source={require('../assets/images/logo.png')} 
              resizeMode="contain" 
              style={{height:Dimensions.get('window').height/ 4, width:Dimensions.get('window').width}} 
            />
          </ImageBackground>
        </Row>
    </Container>
  );
}

const mapStateToProps = (state: AppState) => ({
  auth: state.auth
});

const mapDipatchToProps = (dispatch: Dispatch) => ({
  onSignedIn: () => {
    dispatch(setSignedIn(true));
    dispatch(setLoading(false));
  },
  onSignedOut: () => {
    dispatch(setSignedIn(false));
    dispatch(setLoading(false));
  },
});

export default connect(mapStateToProps, mapDipatchToProps)(Welcome)

const styles = StyleSheet.create({
  image: {
    flex: 1,
    height: Dimensions.get('window').height,
    justifyContent: "center"
  },
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'row',
  },
  formContainer: {
    flex: 1
  },
  headerContainer: {
    flex:1,
    backgroundColor: '#3e9b83',
    height: Dimensions.get('window').height
  }
});